@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700);
body{background:#ececec;text-align:center;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;height:100vh;margin:0;font-family:'Roboto', sans-serif;font-weight:300}#generator{background:#fff;max-width:25em;-webkit-animation:fadeIn 1s ease-in-out;animation:fadeIn 1s ease-in-out;-webkit-backface-visibility:hidden;backface-visibility:hidden;transition:all 0.5s}@-webkit-keyframes fadeIn{0%{opacity:0}100%{opacity:1}}@keyframes fadeIn{0%{opacity:0}100%{opacity:1}}#title{color:#fff;font-size:1.2em;padding:1em;background:#2768df}#rNum{padding:0.8em;font-size:3em;margin:0;color:#212121;transition:padding 0.2s}#inputs,#headers{display:-webkit-flex;display:flex;-webkit-align-content:center;align-content:center;-webkit-flex-flow:row wrap;flex-flow:row wrap;-webkit-justify-content:space-around;justify-content:space-around;color:#212121}#inputs input,#headers input{-webkit-appearance:none;border:0;font-size:1.1em;width:20%;border-bottom:5px solid #2768df;margin-bottom:2em;background:#f0f0f0;padding:0.4em;border-radius:0;-webkit-appearance:none;transition:margin 0.2s}#inputs p,#headers p{font-size:1em;color:#212121;font-weight:400}#inputs #generate,#headers #generate{webkit-appearance:none;width:100%;padding:1em 0;color:#fff;font-size:1.2em;margin:0;display:block;background:#317de0;cursor:pointer;transition:background 0.3s;width:100%}#inputs #generate:hover,#headers #generate:hover{background:#2768df}@media screen and (max-height: 30em){#generator #rNum{padding:0;padding-top:0.1em}#inputs input{margin-bottom:1em}}

